import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'playersFilter'
})
export class PlayersFilterPipe implements PipeTransform {

  transform(playersList, searchStr: string) {
    if (playersList.length === 0 || searchStr === '') {
      return playersList;
    }
    return playersList.filter((player) => player.name.toLowerCase().indexOf(searchStr.trim().toLowerCase()) !== -1);
  }
}
