import {Component, OnChanges, OnInit} from '@angular/core';
import {EditModeService} from "../../../shared/services/edit-mode.service";
import {ModalService} from "../../../shared/services/modal.service";
import {ActivatedRoute, Params} from "@angular/router";
import {IllnessesService} from "../../../shared/services/illnesses.service";
import {BodycheckService} from "../../../shared/services/bodycheck.service";

@Component({
  selector: 'FT-app-player-bodycheck',
  templateUrl: './player-bodycheck.component.html',
  styleUrls: ['./player-bodycheck.component.scss']
})
export class PlayerBodycheckComponent implements OnInit {
  playerId;
  bodycheckList;
  bodycheckId;
  doctorsDiagnosis;
  baks;
  oaks;
  oams;
  uzis;
  ekgs;

  isLoaded = false;


  isImgOpen = false;


  constructor(private editModeService: EditModeService,
              private modalService: ModalService,
              private route: ActivatedRoute,
              private bodycheckService: BodycheckService) {
  }

  openImg() {
    this.isImgOpen = true;
  }

  closeImg() {
    this.isImgOpen = false;
  }


  ngOnInit() {
    this.route.parent.params.subscribe((params: Params) => {
      this.playerId = params['id'];
    });

    this.bodycheckService.getBodychecks(this.playerId).subscribe((data) => {
      this.bodycheckList = data;
      this.isLoaded = true;
    });
  }

  getInfo(event) {
    this.bodycheckId = event.currentTarget.id;

    this.bodycheckService.getDoctorsDiagnosis(this.bodycheckId).subscribe((data) => {
      this.doctorsDiagnosis = data;
    });


    this.bodycheckService.getBak(this.bodycheckId).subscribe((data) => {
      this.baks = data;
    });
    this.bodycheckService.getOak(this.bodycheckId).subscribe((data) => {
      this.oaks = data;
    });
    this.bodycheckService.getOam(this.bodycheckId).subscribe((data) => {
      this.oams = data;
    });
    this.bodycheckService.getUzi(this.bodycheckId).subscribe((data) => {
      this.uzis = data;
    });
    this.bodycheckService.getEkg(this.bodycheckId).subscribe((data) => {
      this.ekgs = data;
    });
  }
}
