import {RouterModule, Routes} from '@angular/router';
import {NgModule} from '@angular/core';
import {SystemComponent} from './system.component';
import {PlayersComponent} from "./players/players.component";
import {PlayerCardComponent} from "./players/player-card/player-card.component";
import {PlayerInfoComponent} from "./players/player-card/player-info/player-info.component";
import {PlayerBodycheckComponent} from "./players/player-card/player-bodycheck/player-bodycheck.component";
import {PlayerIllnessesComponent} from "./players/player-card/player-illnesses/player-illnesses.component";
import {PlayerTestsComponent} from "./players/player-card/player-tests/player-tests.component";
import {EditPlayerComponent} from "./players/player-card/edit-player/edit-player.component";
import {AddPlayerComponent} from "./players/add-player/add-player.component";
import {EmptyComponent} from "./shared/components/empty/empty.component";



const routes: Routes = [
  {
    path: 'system', component: SystemComponent, children: [
      {path: 'add-player', component: AddPlayerComponent},
      {path: '', component: EmptyComponent},
      {
        path: 'players/:id',
        component: PlayerCardComponent,
        children: [
          {path: 'player-info', component: PlayerInfoComponent, data: {animation: 'InfoPage'}},
          {path: 'player-bodycheck', component: PlayerBodycheckComponent, data: {animation: 'BodycheckPage'}},
          {path: 'player-illnesses', component: PlayerIllnessesComponent, data: {animation: 'IllnessPage'}},
          {path: 'player-tests', component: PlayerTestsComponent, data: {animation: 'TestPage'}},
          {path: 'edit-player', component: EditPlayerComponent}
        ]
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})

export class SystemRoutingModule {

}
