import {Component, OnInit} from '@angular/core';
import {EditModeService} from "../../../shared/services/edit-mode.service";
import {ActivatedRoute, Params} from "@angular/router";
import {PlayersService} from "../../../shared/services/players.service";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {GeneralInfoService} from "../../../shared/services/general-info.service";

@Component({
  selector: 'FT-app-player-info',
  templateUrl: './player-info.component.html',
  styleUrls: ['./player-info.component.scss']
})
export class PlayerInfoComponent implements OnInit {
  playerId;
  playerGeneralInfo;
  playerGeneralInfoId;
  form: FormGroup;
  flurographiesList;
  vaccinationsList;
  surgeriesList;

  flurographyForm: FormGroup;
  vaccinationForm: FormGroup;
  surgeryForm: FormGroup;


  constructor(private route: ActivatedRoute,
              private playersService: PlayersService,
              private editModeService: EditModeService,
              private generalInfoService: GeneralInfoService) {
  }

  ngOnInit() {

    this.form = new FormGroup({
      date: new FormControl(),
      weight: new FormControl(),
      height: new FormControl(),
      arterialPressure: new FormControl(),
      bloodType: new FormControl()
    });
    this.flurographyForm = new FormGroup({
      flurographyDate: new FormControl(null, [Validators.required]),
      flurographyInfo: new FormControl(null, [Validators.required])
    });
    this.vaccinationForm = new FormGroup({
      vaccinationDate: new FormControl(null, [Validators.required]),
      vaccinationInfo: new FormControl(null, [Validators.required])
    });
    this.surgeryForm = new FormGroup({
      surgeryDate: new FormControl(null, [Validators.required]),
      surgeryInfo: new FormControl(null, [Validators.required]),
      surgeryDiagnosis: new FormControl(null, [Validators.required])
    });


    this.route.parent.params.subscribe((params: Params) => {
      this.playerId = params['id'];
    });
    this.generalInfoService.getPlayerGeneralInfoById(this.playerId).subscribe((data) => {

      this.playerGeneralInfo =
        {
          id: data.id,
          patientId: data.patientId,
          birthday: data.birthday,
          weight: data.weight,
          height: data.height,
          arterialPressure: data.arterialPressure,
          bloodType: data.bloodType
        };
      this.playerGeneralInfoId = this.playerGeneralInfo.id;
      this.form.patchValue({
        date: data.birthday,
        weight: data.weight,
        height: data.height,
        arterialPressure: data.arterialPressure,
        bloodType: data.bloodType
      });
      this.generalInfoService.getFluorographiesById(this.playerGeneralInfo.id).subscribe((data) => {
        this.flurographiesList = data;
      });
      this.generalInfoService.getVaccinationsById(this.playerGeneralInfo.id).subscribe((data) => {
        this.vaccinationsList = data;
      });
      this.generalInfoService.getSurgeriesById(this.playerGeneralInfo.id).subscribe((data) => {
        this.surgeriesList = data;
      });

    });

  }
  deleteFlurography(event){
    let id = event.target.id;
    this.generalInfoService.deleteFlurographyById(id).subscribe((data) => {
      window.location.reload();
    });
  }
  deleteVaccination(event){
    let id = event.target.id;
    this.generalInfoService.deleteVaccinationById(id).subscribe((data) => {
      window.location.reload();
    });
  }
  deleteSurgery(event){
    let id = event.target.id;
    this.generalInfoService.deleteSurgeryById(id).subscribe((data) => {
      window.location.reload();
    });
  }

  onSubmit() {
    const { weight, height, arterialPressure, bloodType} = this.form.value;

    const newPlayerGeneralInfo = {
      id: this.playerGeneralInfo.id,
      birthday: this.form.value.date.toDateString(),
      weight: Math.abs(weight),
      height: Math.abs(height),
      arterialPressure: Math.abs(arterialPressure),
      bloodType: bloodType
    };


    this.generalInfoService.editPlayerGeneralInfoById(newPlayerGeneralInfo).subscribe(() => {
      this.form.reset();
      this.editModeService.editModeOff();
      window.location.reload();
    });

  }

  onFlurographySubmit() {
    const {flurographyDate, flurographyInfo} = this.flurographyForm.value;

    const newFlurography = {
      procedureTime: flurographyDate.toDateString(),
      information: flurographyInfo,
      generalInfoId: this.playerGeneralInfoId
    };
    if (!this.flurographyForm.invalid) {
      this.generalInfoService.addFluorography(newFlurography.generalInfoId, newFlurography).subscribe(() => {
        this.flurographyForm.reset();
        window.location.reload();
      });
    }

  }

  onVaccinationSubmit() {
    const {vaccinationDate, vaccinationInfo} = this.vaccinationForm.value;

    const newVaccination = {
      procedureTime: vaccinationDate.toDateString(),
      information: vaccinationInfo,
      generalInfoId: this.playerGeneralInfoId
    };

    this.generalInfoService.addVaccination(newVaccination.generalInfoId, newVaccination).subscribe(() => {
      this.vaccinationForm.reset();
      window.location.reload();
    });
  }


  onSurgerySubmit() {
    const {surgeryDate, surgeryInfo, surgeryDiagnosis} = this.surgeryForm.value;

    const newSurgery = {
      procedureTime: surgeryDate.toDateString(),
      interventionType: surgeryInfo,
      diagnosis: surgeryDiagnosis,
      generalInfoId: this.playerGeneralInfoId
    };

    this.generalInfoService.addSurgery(newSurgery.generalInfoId, newSurgery).subscribe(() => {
      this.surgeryForm.reset();
      window.location.reload();
    });
  }

}
