import {Injectable} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {BaseApi} from "../../../shared/core/base-api";
import {Observable} from "rxjs";


@Injectable()

export class IllnessesService  extends BaseApi {

  constructor(public http: HttpClient) {
    super(http);
  }
  getIllnesses(id): Observable<any> {
    return this.get(`Patients/${id}/InjuriesDiseases`);
  }
  addIllness(id, data): Observable<any> {
    return this.post(`Patients/${id}/InjuriesDiseases`, data);
  }

  deleteIllnessById(id): Observable<any>{
    return this.delete(`InjuriesDiseases/${id}`);
  }
  editIllness(data): Observable<any> {
    return this.put(`InjuriesDiseases`, data);
  }
  getIllnessById(id): Observable<any>{
    return this.get(`InjuriesDiseases/${id}`);
  }

  getMrt(id): Observable<any> {
    return this.get(`InjuriesDiseases/${id}/MRIs`);
  }
  getRentgen(id): Observable<any> {
    return this.get(`InjuriesDiseases/${id}/Radiography`);
  }
  getUzi(id): Observable<any> {
    return this.get(`InjuriesDiseases/${id}/CommonUltrasounds`);
  }
}
