import {Injectable} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {BaseApi} from "../../../shared/core/base-api";
import {Observable, Subject} from "rxjs";
import { map } from 'rxjs/operators';

@Injectable()

export class PlayersService  extends BaseApi {
  private addedPlayer = new Subject();
  public addedPlayer$ = this.addedPlayer.asObservable();

  private editedPlayer = new Subject();
  public editedPlayer$ = this.editedPlayer.asObservable();

  constructor(public http: HttpClient) {
    super(http);
  }

  getPlayers(): Observable<any> {
    return this.get('patients');
  }
  getPlayerById(id:string): Observable<any> {
    return this.get(`patients/${id}`);
  }

  addPlayer(player: FormData): Observable<any> {
    return this.post('patients', player)
      .pipe(map( () => {
        this.addedPlayer.next(player);
        return player;
      }));
  }

  editPlayerById(player): Observable<any>{
    return this.put(`patients`, player)
      .pipe(map( () => {
        this.editedPlayer.next(player);
        return player;
      }));
  }
  deletePlayerById(id): Observable<any>{
    return this.delete(`patients/${id}`);
  }

}
