import {Injectable} from '@angular/core';

@Injectable()

export class ModalService {

  isOpenModal = false;
  modalType;
  playerId;

  constructor() {
  }

  openModal(event) {
    this.isOpenModal = true;
    this.modalType = event.target.title;
    this.playerId = event.target.id;
  }
  closeModal() {
    this.isOpenModal = false;
  }
}
