import {Component, OnInit, ViewChild} from '@angular/core';
import {ModalService} from "../../../../shared/services/modal.service";
import {FormBuilder, FormControl, FormGroup, Validators} from "@angular/forms";
import {IllnessesService} from "../../../../shared/services/illnesses.service";

@Component({
  selector: 'FT-app-add-illness',
  templateUrl: './add-illness.component.html',
  styleUrls: ['./add-illness.component.scss']
})
export class AddIllnessComponent implements OnInit {

  playerId;

  selectedFilesMrt;
  selectedFilesUzi;
  selectedFilesRentgen;
  imageSrcMrt;
  imageSrcUzi;
  imageSrcRentgen;

  isIllnessesAdded = false;
  isLoaded = true;

  form = this.fb.group({
    illnessStartDate: ['', Validators.required],
    illnessEndDate: ['', Validators.required],
    diagnosis: '',
    drugTherapy: '',
    physiotherapyTreatment: '',
    other: ''
  });

  @ViewChild('mrt') bak: any;
  @ViewChild('uzi') oak: any;
  @ViewChild('rentgen') oam: any;


  constructor(private modalService: ModalService,
              private illnessesService: IllnessesService,
              private fb: FormBuilder) {
  }

  ngOnInit() {
    this.playerId = this.modalService.playerId;
  }
  runPleloader() {
    this.isLoaded = false;
  }

  onFileChangeMrt(event){
    let nativeElement: HTMLInputElement = this.bak.nativeElement;

    this.selectedFilesMrt = nativeElement.files;

    if (event.target.files && event.target.files[0]) {
      const percentDone = Math.round(100 * event.loaded / event.total);

      const file = event.target.files[0];

      const reader = new FileReader();
      reader.onload = e => this.imageSrcMrt = reader.result;

      reader.readAsDataURL(file);
    }
  }
  onFileChangeUzi(event){
    let nativeElement: HTMLInputElement = this.oak.nativeElement;

    this.selectedFilesUzi = nativeElement.files;

    if (event.target.files && event.target.files[0]) {

      const file = event.target.files[0];

      const reader = new FileReader();
      reader.onload = e => this.imageSrcUzi = reader.result;

      reader.readAsDataURL(file);
    }
  }
  onFileChangeRentgen(event){
    let nativeElement: HTMLInputElement = this.oam.nativeElement;

    this.selectedFilesRentgen = nativeElement.files;

    if (event.target.files && event.target.files[0]) {

      const file = event.target.files[0];

      const reader = new FileReader();
      reader.onload = e => this.imageSrcRentgen = reader.result;

      reader.readAsDataURL(file);
    }
  }


  onSubmit() {

    const {illnessStartDate, illnessEndDate, diagnosis, drugTherapy, physiotherapyTreatment, other} = this.form.value;

    let input = new FormData();



    const selectedFilesMrt = [
      {
        file: this.selectedFilesMrt,
        info: ""
      }
    ];
    const selectedFilesUzi = [
      {
        file: this.selectedFilesUzi,
        info: ""
      }
    ];
    const selectedFilesRentgen = [
      {
        file: this.selectedFilesRentgen,
        info: ""
      }
    ];



    input.append("dateInjuriesOrDiseases", illnessStartDate);
    input.append("disabilityType", "Injury");
    input.append("releasedInMainGroup", illnessEndDate);


    if (diagnosis === '') {
      input.append("diagnosis", 'Нет информации');
    } else {
      input.append("diagnosis", diagnosis);
    }


    if (drugTherapy === '') {
      input.append("drugTherapy", 'Нет информации');
    } else {
      input.append("drugTherapy", drugTherapy);
    }
    if (other === '') {
      input.append("other", 'Нет информации');
    } else {
      input.append("other", other);
    }
    if (physiotherapyTreatment === '') {
      input.append("physiotherapyTreatment", 'Нет информации');
    } else {
      input.append("physiotherapyTreatment", physiotherapyTreatment);
    }



    for (let i = 0; i < selectedFilesMrt.length; i++) {
      if (selectedFilesMrt[i].file == undefined) {
        input.append("mrIs[" + i + "]." + "file", null);
      } else{
        input.append("mrIs[" + i + "]." + "file", selectedFilesMrt[i].file[i]);
      }

      input.append("mrIs[" + i + "]." + "info", selectedFilesMrt[i].info);
    }

    for (let i = 0; i < selectedFilesUzi.length; i++) {

      if (selectedFilesUzi[i].file == undefined) {
        input.append("CommonUltrasounds[" + i + "]." + "file", null);
      } else{
        input.append("CommonUltrasounds[" + i + "]." + "file", selectedFilesUzi[i].file[i]);
      }

      input.append("CommonUltrasounds[" + i + "]." + "info", selectedFilesUzi[i].info);
    }


    for (let i = 0; i < selectedFilesRentgen.length; i++) {


      if (selectedFilesRentgen[i].file == undefined) {
        input.append("Radiographies[" + i + "]." + "file", null);
      } else{
        input.append("Radiographies[" + i + "]." + "file", selectedFilesRentgen[i].file[i]);
      }

      input.append("Radiographies[" + i + "]." + "info", selectedFilesRentgen[i].info);

    }

    this.illnessesService.addIllness(this.playerId, input).subscribe(() => {
      this.form.reset();
      this.isIllnessesAdded = true;
      this.isLoaded = true;
      window.location.reload();
    });
  }
}
