import {Component, OnInit} from '@angular/core';
import {ModalService} from "../../../shared/services/modal.service";
import {ActivatedRoute, Params} from "@angular/router";
import {IllnessesService} from "../../../shared/services/illnesses.service";

@Component({
  selector: 'FT-app-player-illnesses',
  templateUrl: './player-illnesses.component.html',
  styleUrls: ['./player-illnesses.component.scss']
})
export class PlayerIllnessesComponent implements OnInit {
  playerId;
  illnessesList;
  illnessId;

  mrts;
  uzis;
  rentgens;

  isLoaded = false;

  constructor(private modalService: ModalService,
              private route: ActivatedRoute,
              private illnessesService: IllnessesService) {
  }

  getIllnessId(event) {
    this.illnessId = event.currentTarget.id;
    this.illnessesService.getMrt(this.illnessId).subscribe((data) => {
      this.mrts = data;
    });
    this.illnessesService.getUzi(this.illnessId).subscribe((data) => {
      this.uzis = data;
    });
    this.illnessesService.getRentgen(this.illnessId).subscribe((data) => {
      this.rentgens = data;
    });
  }

  ngOnInit() {
    this.route.parent.params.subscribe((params: Params) => {
      this.playerId = params['id'];
    });

    this.illnessesService.getIllnesses(this.playerId).subscribe((data) => {
      this.isLoaded = true;
      this.illnessesList = data;

    });




  }


}
