import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {ActivatedRoute, Params, RouterOutlet} from "@angular/router";
import {PlayersService} from "../../shared/services/players.service";
import {ModalService} from "../../shared/services/modal.service";
import {EditModeService} from "../../shared/services/edit-mode.service";
import {animate, animateChild, group, query, style, transition, trigger} from "@angular/animations";
import {GeneralInfoService} from "../../shared/services/general-info.service";

@Component({
  selector: 'FT-app-player-card',
  templateUrl: './player-card.component.html',
  styleUrls: ['./player-card.component.scss'],
  animations: [
    trigger('routeAnimations', [
      transition('* <=> *', [
        style({ position: 'relative' }),
        query(':enter, :leave', [
          style({
            position: 'absolute',
            top: 0,
            left: 0,
            width: '100%'
          })
        ], { optional: true }),
        query(':enter', [
          style({ left: '-100%'})
        ], { optional: true }),
        group([
          query(':leave',  [
            animate('300ms ease-out', style({ left: '100%'}))
          ], { optional: true }),
          query(':enter', [
            animate('300ms ease-out', style({ left: '0%'}))
          ], { optional: true })
        ])
      ])
    ])
  ]
})
export class PlayerCardComponent implements OnInit {
  playerId;
  playerName;
  playerPhoto;
  @Output() OnBodycheckAdd = new EventEmitter();

  constructor(private route: ActivatedRoute,
              private playersService: PlayersService,
              private modalService: ModalService,
              private editModeService: EditModeService
  ) {
  }


  prepareRoute(outlet: RouterOutlet) {
    return outlet && outlet.activatedRouteData && outlet.activatedRouteData['animation'];
  }

  newBodycheckAdded(bodycheck){
    this.OnBodycheckAdd.emit(bodycheck);
  }


  ngOnInit() {
    this.route.params.subscribe((params: Params) => {
      this.playerId = params['id'];
      this.playersService.getPlayerById(this.playerId).subscribe((data) => {
        this.playerName = data.name;
        this.playerPhoto = 'https://football-team.azurewebsites.net/'+data.photo;

      });

    })
  }


}
