import {Injectable} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {BaseApi} from "../../../shared/core/base-api";
import {Observable} from "rxjs";


@Injectable()

export class GeneralInfoService extends BaseApi {

  constructor(public http: HttpClient) {
    super(http);
  }

  getPlayerGeneralInfoById(id: string): Observable<any> {
    return this.get(`patients/${id}/generalInformation`);
  }

  editPlayerGeneralInfoById(info): Observable<any> {
    return this.put(`generalInformation`, info);
  }

  getFluorographiesById(id) {
    return this.get(`generalInformation/${id}/Fluorography`);
  }
  getVaccinationsById(id) {
    return this.get(`generalInformation/${id}/VaccinationStatus`);
  }
  getSurgeriesById(id) {
    return this.get(`generalInformation/${id}/SurgicalIntervention`);
  }
  addFluorography(id, data) {
    return this.post(`generalInformation/${id}/Fluorography`, data);
  }
  addVaccination(id, data) {
    return this.post(`generalInformation/${id}/VaccinationStatus`, data);
  }
  addSurgery(id, data) {
    return this.post(`generalInformation/${id}/SurgicalIntervention`, data);
  }
  deleteFlurographyById(id): Observable<any>{
    return this.delete(`Fluorography/${id}`);
  }
  deleteVaccinationById(id): Observable<any>{
    return this.delete(`VaccinationStatus/${id}`);
  }
  deleteSurgeryById(id): Observable<any>{
    return this.delete(`SurgicalIntervention/${id}`);
  }
}
