import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {Observable} from 'rxjs';

@Injectable()
export class BaseApi {
  private baseUrl = 'http://dasha1234-001-site1.gtempurl.com/api/';

  constructor(public http: HttpClient) {
  }

  private getUrl(url: string = ''): string {
    return this.baseUrl + url;
  }

  public get(url: string = ''): Observable<any> {
    return this.http.get(this.getUrl(url));
  }

  public post(url: string = '', data: any = {}, headers: any = ''): Observable<any> {
    return this.http.post(this.getUrl(url), data, headers );
  }

  public put(url: string = '', data: any = {}): Observable<any> {
    return this.http.put<any>(this.getUrl(url), data);
  }
  public delete(url: string = ''): Observable<any> {
    return this.http.delete<any>(this.getUrl(url));
  }
}
