import {Component, OnInit, ViewChild} from '@angular/core';
import {ModalService} from "../../../../shared/services/modal.service";
import {FormBuilder, FormControl, FormGroup, Validators} from "@angular/forms";
import {BodycheckService} from "../../../../shared/services/bodycheck.service";

@Component({
  selector: 'FT-app-edit-bodycheck',
  templateUrl: './edit-bodycheck.component.html',
  styleUrls: ['./edit-bodycheck.component.scss']
})
export class EditBodycheckComponent implements OnInit {

  playerId;
  bodycheckId;
  doctorsDiagnosisList = [];
  bodycheck;
  doctors;

  selectedFilesBak;
  selectedFilesOak;
  selectedFilesOam;
  selectedFilesEkg;
  selectedFilesUzi;
  imageSrcBak;
  imageSrcOak;
  imageSrcOam;
  imageSrcEkg;
  imageSrcUzi;


  filesOak;
  filesBak;
  filesOam;
  filesUzi;
  filesEkg;

  isBodycheckEdited;
  isLoaded;


  form = this.fb.group({
    procedureTime: ['', Validators.required],
    bodycheckAllow: '',
    Oculist: '',
    Neurologist: '',
    Otolaryngologist: '',
    Surgeon: '',
    Dentist: '',
    Endocrinologist: '',
    SportsDoctor: ''
  });

  @ViewChild('bak') bak: any;
  @ViewChild('oak') oak: any;
  @ViewChild('oam') oam: any;
  @ViewChild('ekg') ekg: any;
  @ViewChild('uzi') uzi: any;

  constructor(private modalService: ModalService,
              private bodycheckService: BodycheckService,
              private fb: FormBuilder) {
  }

  ngOnInit() {
    this.isLoaded = true;

    this.bodycheckId = this.modalService.playerId;


    this.bodycheckService.getBodycheckById(this.bodycheckId).subscribe((data) => {

      this.bodycheck = data;

      this.form.patchValue({
        procedureTime: data.procedureTime,
        bodycheckAllow: data.allowance
      });


    });
    this.bodycheckService.getDoctorsDiagnosis(this.bodycheckId).subscribe((data) => {
      this.doctors = data;
      this.form.patchValue({
        Oculist: data[0].diagnosis,
        Neurologist: data[1].diagnosis,
        Otolaryngologist: data[2].diagnosis,
        Surgeon: data[3].diagnosis,
        Dentist: data[4].diagnosis,
        Endocrinologist: data[5].diagnosis,
        SportsDoctor: data[6].diagnosis
      });
    });
    this.bodycheckService.getDoctorsDiagnosis(this.bodycheckId).subscribe((data) => {
      this.doctorsDiagnosisList = [
        {
          medicalProfession: "Oculist",
          id: data[0].id
        },
        {
          medicalProfession: "Neurologist",
          id: data[1].id
        },
        {
          medicalProfession: "Otolaryngologist",
          id: data[2].id
        },
        {
          medicalProfession: "Surgeon",
          id: data[3].id
        },
        {
          medicalProfession: "Dentist",
          id: data[4].id
        },
        {
          medicalProfession: "Endocrinologist",
          id: data[5].id
        },
        {
          medicalProfession: "SportsDoctor",
          id: data[6].id
        }
      ];
    });
    this.bodycheckService.getOak(this.bodycheckId).subscribe((data) => {
      const selectedFilesOak = [
        {
          file: this.selectedFilesOak,
          info: "",
          id: data[0].id
        }
      ];
      this.filesOak = selectedFilesOak;
    });
    this.bodycheckService.getBak(this.bodycheckId).subscribe((data) => {
      const selectedFilesBak = [
        {
          file: this.selectedFilesBak,
          info: "",
          id: data[0].id
        }
      ];
      this.filesBak = selectedFilesBak;
    });
    this.bodycheckService.getOam(this.bodycheckId).subscribe((data) => {
      const selectedFilesOam = [
        {
          file: this.selectedFilesOam,
          info: "",
          id: data[0].id
        }
      ];
      this.filesOam = selectedFilesOam;
    });
    this.bodycheckService.getUzi(this.bodycheckId).subscribe((data) => {
      if (data.length > 0) {
        this.filesUzi = [
          {
            file: this.selectedFilesUzi,
            info: "",
            id: data[0].id
          }
        ];
      } else {
        this.filesUzi = [
          {
            file: null,
            info: "",
            id: null
          }
        ];
      }

    });
    this.bodycheckService.getEkg(this.bodycheckId).subscribe((data) => {
      const selectedFilesEkg = [
        {
          file: this.selectedFilesEkg,
          info: "",
          id: data[0].id
        }
      ];
      this.filesEkg = selectedFilesEkg;
    });

  }

  runPleloader() {
    this.isLoaded = false;
  }

  onFileChangeBak(event) {
    let nativeElement: HTMLInputElement = this.bak.nativeElement;

    this.selectedFilesBak = nativeElement.files;

    if (event.target.files && event.target.files[0]) {
      const percentDone = Math.round(100 * event.loaded / event.total);

      const file = event.target.files[0];

      const reader = new FileReader();
      reader.onload = e => this.imageSrcBak = reader.result;

      reader.readAsDataURL(file);
    }
  }

  onFileChangeOak(event) {
    let nativeElement: HTMLInputElement = this.oak.nativeElement;

    this.selectedFilesOak = nativeElement.files;

    if (event.target.files && event.target.files[0]) {

      const file = event.target.files[0];

      const reader = new FileReader();
      reader.onload = e => this.imageSrcOak = reader.result;

      reader.readAsDataURL(file);
    }
  }

  onFileChangeOam(event) {
    let nativeElement: HTMLInputElement = this.oam.nativeElement;

    this.selectedFilesOam = nativeElement.files;

    if (event.target.files && event.target.files[0]) {

      const file = event.target.files[0];

      const reader = new FileReader();
      reader.onload = e => this.imageSrcOam = reader.result;

      reader.readAsDataURL(file);
    }
  }

  onFileChangeEkg(event) {
    let nativeElement: HTMLInputElement = this.ekg.nativeElement;

    this.selectedFilesEkg = nativeElement.files;

    if (event.target.files && event.target.files[0]) {


      const file = event.target.files[0];

      const reader = new FileReader();
      reader.onload = e => this.imageSrcEkg = reader.result;

      reader.readAsDataURL(file);
    }
  }

  onFileChangeUzi(event) {
    let nativeElement: HTMLInputElement = this.uzi.nativeElement;

    this.selectedFilesUzi = nativeElement.files;

    if (event.target.files && event.target.files[0]) {

      const file = event.target.files[0];

      const reader = new FileReader();
      reader.onload = e => this.imageSrcUzi = reader.result;

      reader.readAsDataURL(file);
    }
  }


  onSubmit() {

    const {procedureTime, bodycheckAllow, Oculist, Neurologist, Otolaryngologist, Surgeon, Dentist, Endocrinologist, SportsDoctor} = this.form.value;

    const selectedFilesOak = [
      {
        file: this.selectedFilesOak,
        info: ""
      }
    ];
    const selectedFilesBak = [
      {
        file: this.selectedFilesBak,
        info: ""
      }
    ];
    const selectedFilesOam = [
      {
        file: this.selectedFilesOam,
        info: ""
      }
    ];
    const selectedFilesUzi = [
      {
        file: this.selectedFilesUzi,
        info: ""
      }
    ];
    const selectedFilesEkg = [
      {
        file: this.selectedFilesEkg,
        info: ""
      }
    ];


    let input = new FormData();

    // let MyDateString;
    // console.log(procedureTime);
    //
    // if (procedureTime) {
    //   let MyDate = procedureTime;
    //
    //   MyDate.setDate(MyDate.getDate());
    //
    //   MyDateString = ('0' + (MyDate.getMonth() + 1)).slice(-2) + '/' + ('0' + MyDate.getDate()).slice(-2) + '/' + MyDate.getFullYear();
    //
    // }

    input.append("procedureTime", procedureTime);


    if (bodycheckAllow === null) {
      input.append("allowance", 'false');
    } else if (bodycheckAllow === true) {
      input.append("allowance", 'true');
    } else {
      input.append("allowance", 'false');
    }

    input.append("id", this.bodycheckId);


    const doctorsDiagnosisList = [
      {
        diagnosis: Oculist,
        medicalProfession: "Oculist",
        id: this.doctorsDiagnosisList[0].id
      },
      {
        diagnosis: Neurologist,
        medicalProfession: "Neurologist",
        id: this.doctorsDiagnosisList[1].id
      },
      {
        diagnosis: Otolaryngologist,
        medicalProfession: "Otolaryngologist",
        id: this.doctorsDiagnosisList[2].id
      },
      {
        diagnosis: Surgeon,
        medicalProfession: "Surgeon",
        id: this.doctorsDiagnosisList[3].id
      },
      {
        diagnosis: Dentist,
        medicalProfession: "Dentist",
        id: this.doctorsDiagnosisList[4].id
      },
      {
        diagnosis: Endocrinologist,
        medicalProfession: "Endocrinologist",
        id: this.doctorsDiagnosisList[5].id
      },
      {
        diagnosis: SportsDoctor,
        medicalProfession: "SportsDoctor",
        id: this.doctorsDiagnosisList[6].id
      }
    ];

    for (let i = 0; i < doctorsDiagnosisList.length; i++) {

      if (doctorsDiagnosisList[i].diagnosis === '') {
        input.append("doctorsDiagnoses[" + i + "]." + "diagnosis", 'Нет информации');
      } else {
        input.append("doctorsDiagnoses[" + i + "]." + "diagnosis", doctorsDiagnosisList[i].diagnosis);
      }
      input.append("doctorsDiagnoses[" + i + "]." + "id", doctorsDiagnosisList[i].id);
      input.append("doctorsDiagnoses[" + i + "]." + "medicalProfession", doctorsDiagnosisList[i].medicalProfession);
    }


    for (let i = 0; i < selectedFilesBak.length; i++) {

      if (selectedFilesBak[i].file == undefined) {
        input.append("bloodChemistryAnalyses[" + i + "]." + "file", null);
      } else {
        input.append("bloodChemistryAnalyses[" + i + "]." + "file", selectedFilesBak[i].file[i]);
      }
      input.append("bloodChemistryAnalyses[" + i + "]." + "info", selectedFilesBak[i].info);
      input.append("bloodChemistryAnalyses[" + i + "]." + "id", this.filesBak[i].id);
    }


    for (let i = 0; i < selectedFilesOak.length; i++) {

      if (selectedFilesOak[i].file == undefined) {
        input.append("generalBloodAnalyses[" + i + "]." + "file", null);
      } else {
        input.append("generalBloodAnalyses[" + i + "]." + "file", selectedFilesOak[i].file[i]);
      }


      input.append("generalBloodAnalyses[" + i + "]." + "info", selectedFilesOak[i].info);
      input.append("generalBloodAnalyses[" + i + "]." + "id", this.filesOak[i].id);
    }

    for (let i = 0; i < selectedFilesOam.length; i++) {

      if (selectedFilesOam[i].file == undefined) {
        input.append("generalUrineAnalyses[" + i + "]." + "file", null);
      } else {
        input.append("generalUrineAnalyses[" + i + "]." + "file", selectedFilesOam[i].file[i]);
      }

      input.append("generalUrineAnalyses[" + i + "]." + "info", selectedFilesOam[i].info);
      input.append("generalUrineAnalyses[" + i + "]." + "id", this.filesOam[i].id);
    }


    for (let i = 0; i < selectedFilesUzi.length; i++) {

      if (selectedFilesUzi[i].file == undefined) {
        input.append("heartUltrasounds[" + i + "]." + "file", null);
      } else {
        input.append("heartUltrasounds[" + i + "]." + "file", selectedFilesUzi[i].file[i]);
      }

      input.append("heartUltrasounds[" + i + "]." + "info", selectedFilesUzi[i].info);
      input.append("heartUltrasounds[" + i + "]." + "id", this.filesUzi[i].id);
    }

    for (let i = 0; i < selectedFilesEkg.length; i++) {

      if (selectedFilesEkg[i].file == undefined) {
        input.append("electrocardiograms[" + i + "]." + "file", null);
      } else {
        input.append("electrocardiograms[" + i + "]." + "file", selectedFilesEkg[i].file[i]);
      }

      input.append("electrocardiograms[" + i + "]." + "info", selectedFilesEkg[i].info);
      input.append("electrocardiograms[" + i + "]." + "id", this.filesEkg[i].id);
    }


    this.bodycheckService.editBodycheck(input).subscribe(() => {
      this.form.reset();
      this.isBodycheckEdited = true;
      this.isLoaded = true;
      window.location.reload();
    });

  }

}
