import {Injectable} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {BaseApi} from "../../../shared/core/base-api";
import {Observable} from "rxjs";


@Injectable()

export class BodycheckService  extends BaseApi {

  constructor(public http: HttpClient) {
    super(http);
  }
  getBodychecks(id): Observable<any> {
    return this.get(`Patients/${id}/MedicalExamination`);
  }
  addBodycheck(id, data): Observable<any> {
    return this.post(`Patients/${id}/MedicalExamination`, data);
  }
  getDoctorsDiagnosis(id): Observable<any> {
    return this.get(`MedicalExamination/${id}/DoctorsDiagnosis`);
  }
  deleteBodycheckById(id): Observable<any>{
    return this.delete(`MedicalExamination/${id}`);
  }
  editBodycheck(data): Observable<any> {
    return this.put(`MedicalExamination`, data);
  }
  getBodycheckById(id): Observable<any>{
    return this.get(`MedicalExamination/${id}`);
  }

  getBak(id): Observable<any> {
    return this.get(`MedicalExamination/${id}/bloodChemistryAnalysis`);
  }
  getOak(id): Observable<any> {
    return this.get(`MedicalExamination/${id}/generalBloodAnalysis`);
  }
  getOam(id): Observable<any> {
    return this.get(`MedicalExamination/${id}/generalUrineAnalysis`);
  }
  getEkg(id): Observable<any> {
    return this.get(`MedicalExamination/${id}/Electrocardiogram`);
  }
  getUzi(id): Observable<any> {
    return this.get(`MedicalExamination/${id}/HeartUltrasound`);
  }

}
