import {Component, OnInit, ViewChild} from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from "@angular/forms";
import {IllnessesService} from "../../../../shared/services/illnesses.service";
import {ModalService} from "../../../../shared/services/modal.service";

@Component({
  selector: 'FT-app-edit-illness',
  templateUrl: './edit-illness.component.html',
  styleUrls: ['./edit-illness.component.scss']
})
export class EditIllnessComponent implements OnInit {

  playerId;
  illnessId;
  illness;

  selectedFilesMrt;
  selectedFilesUzi;
  selectedFilesRentgen;
  imageSrcMrt;
  imageSrcUzi;
  imageSrcRentgen;

  form = this.fb.group({
    illnessStartDate: ['', Validators.required],
    illnessEndDate: ['', Validators.required],
    diagnosis: '',
    drugTherapy: '',
    physiotherapyTreatment: '',
    other: ''
  });

  filesMrt;
  filesUzi;
  filesRentgen;


  isIllnessesEdited = false;
  isLoaded = true;


  @ViewChild('mrt') bak: any;
  @ViewChild('uzi') oak: any;
  @ViewChild('rentgen') oam: any;


  constructor( private illnessesService: IllnessesService,
               private modalService: ModalService,
               private fb: FormBuilder) {

  }
  runPleloader() {
    this.isLoaded = false;
  }

  ngOnInit() {

    this.illnessId = this.modalService.playerId;


    this.illnessesService.getIllnessById(this.illnessId).subscribe((data) => {
      this.illness = data;
      this.form.patchValue({
        illnessStartDate: data.dateInjuriesOrDiseases,
        illnessEndDate: data.releasedInMainGroup,
        diagnosis: data.diagnosis,
        drugTherapy: data.drugTherapy,
        physiotherapyTreatment: data.physiotherapyTreatment,
        other: data.other
      });

    });

    this.illnessesService.getMrt(this.illnessId).subscribe((data) => {
      this.filesMrt = [
        {
          file: this.selectedFilesMrt,
          info: "",
          id: data[0].id
        }
      ];
    });
    this.illnessesService.getUzi(this.illnessId).subscribe((data) => {
      this.filesUzi = [
        {
          file: this.selectedFilesUzi,
          info: "",
          id: data[0].id
        }
      ];
    });
    this.illnessesService.getRentgen(this.illnessId).subscribe((data) => {
      this.filesRentgen = [
        {
          file: this.selectedFilesRentgen,
          info: "",
          id: data[0].id
        }
      ];
    });


  }
  onFileChangeMrt(event){
    let nativeElement: HTMLInputElement = this.bak.nativeElement;

    this.selectedFilesMrt = nativeElement.files;

    if (event.target.files && event.target.files[0]) {
      const percentDone = Math.round(100 * event.loaded / event.total);

      const file = event.target.files[0];

      const reader = new FileReader();
      reader.onload = e => this.imageSrcMrt = reader.result;

      reader.readAsDataURL(file);
    }
  }
  onFileChangeUzi(event){
    let nativeElement: HTMLInputElement = this.oak.nativeElement;

    this.selectedFilesUzi = nativeElement.files;

    if (event.target.files && event.target.files[0]) {

      const file = event.target.files[0];

      const reader = new FileReader();
      reader.onload = e => this.imageSrcUzi = reader.result;

      reader.readAsDataURL(file);
    }
  }
  onFileChangeRentgen(event){
    let nativeElement: HTMLInputElement = this.oam.nativeElement;

    this.selectedFilesRentgen = nativeElement.files;

    if (event.target.files && event.target.files[0]) {

      const file = event.target.files[0];

      const reader = new FileReader();
      reader.onload = e => this.imageSrcRentgen = reader.result;

      reader.readAsDataURL(file);
    }
  }

  onSubmit() {

    const {illnessStartDate, illnessEndDate, diagnosis, drugTherapy, physiotherapyTreatment, other} = this.form.value;
    let input = new FormData();

    input.append("dateInjuriesOrDiseases", illnessStartDate);
    input.append("disabilityType", "Injury");
    input.append("releasedInMainGroup", illnessEndDate);

    if (diagnosis === '') {
      input.append("diagnosis", 'Нет информации');
    } else {
      input.append("diagnosis", diagnosis);
    }


    if (drugTherapy === '') {
      input.append("drugTherapy", 'Нет информации');
    } else {
      input.append("drugTherapy", drugTherapy);
    }
    if (other === '') {
      input.append("other", 'Нет информации');
    } else {
      input.append("other", other);
    }
    if (physiotherapyTreatment === '') {
      input.append("physiotherapyTreatment", 'Нет информации');
    } else {
      input.append("physiotherapyTreatment", physiotherapyTreatment);
    }


    input.append("id", this.illnessId);



    for (let i = 0; i < this.filesMrt.length; i++) {
      if (this.filesMrt[i].file == undefined) {
        input.append("MRIs[" + i + "]." + "file", null);
      }else{
        input.append("MRIs[" + i + "]." + "file", this.filesMrt[i].file);
      }
      input.append("MRIs[" + i + "]." + "info", this.filesMrt[i].info);
      input.append("MRIs[" + i + "]." + "id", this.filesMrt[i].id);
    }


    for (let i = 0; i < this.filesUzi.length; i++) {
      if (this.filesUzi[i].file == undefined) {
        input.append("CommonUltrasounds[" + i + "]." + "file", null);
      }else{
        input.append("CommonUltrasounds[" + i + "]." + "file", this.filesUzi[i].file);
      }
      input.append("CommonUltrasounds[" + i + "]." + "info", this.filesUzi[i].info);
      input.append("CommonUltrasounds[" + i + "]." + "id", this.filesUzi[i].id);
    }
    for (let i = 0; i < this.filesRentgen.length; i++) {
      if (this.filesRentgen[i].file == undefined) {
        input.append("Radiographies[" + i + "]." + "file", null);
      }else{
        input.append("Radiographies[" + i + "]." + "file", this.filesRentgen[i].file);
      }
      input.append("Radiographies[" + i + "]." + "info", this.filesRentgen[i].info);
      input.append("Radiographies[" + i + "]." + "id", this.filesRentgen[i].id);
    }



    this.illnessesService.editIllness(input).subscribe(() => {
      this.form.reset();
      this.isIllnessesEdited = true;
      this.isLoaded = true;
      window.location.reload();
    });
  }


}
