import {Component, OnInit} from '@angular/core';
import {PlayersService} from "../shared/services/players.service";
import {SearchService} from "../shared/services/search.service";
import {EditModeService} from "../shared/services/edit-mode.service";
import {ActivatedRoute, NavigationStart, Router} from "@angular/router";
import {Observable} from "rxjs";
import {filter} from "rxjs/operators";

@Component({
  selector: 'FT-app-players',
  templateUrl: './players.component.html',
  styleUrls: ['./players.component.scss']
})
export class PlayersComponent implements OnInit {

  players: Array<any> = [];
  searchPlayer = '';
  isLoaded = false;
  navStart;


  constructor(private playersService: PlayersService,
              private searchService: SearchService,
              private editModeService: EditModeService,
              private route: ActivatedRoute,
              private router: Router) {
    this.navStart = router.events.pipe(
      filter(evt => evt instanceof NavigationStart)
    ) as Observable<NavigationStart>;
  }


  ngOnInit() {
    this.playersService.getPlayers().subscribe((data) => {
      this.players = data;
      this.isLoaded = true;
    });

    this.playersService.addedPlayer$.subscribe((player) => {
      this.players.push(player);
    });

    this.playersService.editedPlayer$.subscribe((player) => {
      // console.log(player);
      // let playerEdited = this.players.find(x => x.id === player);
    });

    this.route.url.subscribe((url) => {
      this.router.navigate([`/system/players/${this.route.snapshot.children[0].params.id}/player-info`]);
    });
  }

}
