import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {ModalService} from "../../../shared/services/modal.service";
import {PlayersService} from "../../../shared/services/players.service";
import {BodycheckService} from "../../../shared/services/bodycheck.service";
import {IllnessesService} from "../../../shared/services/illnesses.service";
import {EditModeService} from "../../../shared/services/edit-mode.service";
import {Router} from "@angular/router";

@Component({
  selector: 'FT-app-edit-modal',
  templateUrl: './edit-modal.component.html',
  styleUrls: ['./edit-modal.component.scss']
})
export class EditModalComponent implements OnInit {
  playerId;
  showTip = false;

  @Output() OnBodycheckAdd = new EventEmitter();

  constructor(private modalService: ModalService,
              private playersService: PlayersService,
              private bodycheckService: BodycheckService,
              private illnessesService: IllnessesService,
              private editModeService: EditModeService,
              private router: Router
  ) {
  }

  newBodycheckAdded(bodycheck) {
    this.OnBodycheckAdd.emit(bodycheck);
  }


  ngOnInit() {
    this.playerId = this.modalService.playerId;
  }

  deletePlayer() {
    this.playersService.deletePlayerById(this.playerId).subscribe(() => {

      this.showTip = true;
      this.editModeService.editModeOff();
      this.router.navigate(['/system']);
      setTimeout("window.location.reload();", 500);

    });
  }

  deleteBodycheck() {
    this.bodycheckService.deleteBodycheckById(this.playerId).subscribe(() => {

      this.showTip = true;
      window.location.reload();
    });
  }

  deleteIllness() {
    this.illnessesService.deleteIllnessById(this.playerId).subscribe(() => {

      this.showTip = true;
      window.location.reload();

    });
  }

}
