import {ElementRef, Component, OnInit, ViewChild, EventEmitter, Output} from '@angular/core';
import {PlayersService} from "../../shared/services/players.service";
import {FormBuilder, FormControl, FormGroup, Validators} from "@angular/forms";
import {Observable} from "rxjs";
import {UploadService} from "../../shared/services/upload.service";


@Component({
  selector: 'FT-app-add-player',
  templateUrl: './add-player.component.html',
  styleUrls: ['./add-player.component.scss']
})
export class AddPlayerComponent implements OnInit {

  @Output() OnPlayerAdd = new EventEmitter();

  selectedFile;
  imageSrc;
  formGroup = this.fb.group({
    firstName: ['', Validators.required],
    lastName: ['', Validators.required]
  });
  isPlayerAdded = false;
  isLoaded = true;

  @ViewChild('fileInput') fileInput: any;


  constructor(private playersService: PlayersService,
              private fb: FormBuilder,
              private fileService: UploadService) {
  }

  ngOnInit() {

  }

  addLoader() {
    this.isLoaded = false;
  }

  onFileChange(event) {
    let nativeElement: HTMLInputElement = this.fileInput.nativeElement;

    this.selectedFile = nativeElement.files;

    if (event.target.files && event.target.files[0]) {
      const percentDone = Math.round(100 * event.loaded / event.total);

      const file = event.target.files[0];

      const reader = new FileReader();
      reader.onload = e => this.imageSrc = reader.result;

      reader.readAsDataURL(file);
    }

  }


  onSubmit() {

    const {firstName, lastName} = this.formGroup.value;

    let input = new FormData();

    if (this.selectedFile === undefined) {
      input.append("file", null);
    } else {
      input.append("file", this.selectedFile[0]);
    }


    input.append("name", firstName + ' ' + lastName);


    if (!this.formGroup.invalid) {
      this.playersService.addPlayer(input).subscribe((data) => {
        this.formGroup.reset();
        this.isPlayerAdded = true;
        this.isLoaded = true;
         window.location.reload();
      });
    }

  }
}
