import { Component, OnInit } from '@angular/core';
import {EditModeService} from "../../../shared/services/edit-mode.service";

@Component({
  selector: 'FT-app-edit-player',
  templateUrl: './edit-player.component.html',
  styleUrls: ['./edit-player.component.scss']
})
export class EditPlayerComponent implements OnInit {

  constructor(private editModeService: EditModeService) { }

  ngOnInit() {
  }

  onEditMode() {
    this.editModeService.editModeOn();
  }
  offEditMode() {
    this.editModeService.editModeOff();
  }
}
